import React, { useState } from 'react';
import type { NextPage } from 'next';
import { ButtonLoading } from '@react-page/editor';
import { useRouter } from 'next/router';
import Link from 'next/link';

const Home: NextPage = () => {
  const router = useRouter();
  const [signInLoad, setSignInLoad] = useState(false);
  const [signUpLoad, setSignUpLoad] = useState(false);
  const handleSignInClick = () => {
    setSignInLoad(true);
    // router.push({
    //   pathname: '/sign-in',
    // });
  };

  const handleSignUpClick = () => {
    setSignUpLoad(true);
    // router.push({
    //   pathname: '/sign-up',
    // });
  };

  return (
    <div
      className="flex min-h-screen flex-col items-center justify-center p-[24px]"
      style={{ fontFamily: 'RocGrotesk-Regular' }}
    >
      <div className="flex flex-col w-full h-[100vh] items-center justify-center bg-gray gap-[30px]">
        <div className="flex flex-col py-0 px-5 items-center justify-start gap-[8px]">
          <img
            className="w-[240px] shrink-0 object-cover"
            alt="MINT CHIP"
            src="/images/mintchip-beta-logo.png"
          />
        </div>
        <div className="text-gray-2">
          Welcome to MINTCHIP! We are so excited to have you.
        </div>
        <div className="flex flex-row items-center space-x-5 w-full justify-center">
          <Link href="/sign-in">
            <button
              onClick={() => handleSignInClick()}
              className="text-gray-2 bg-primary-blue h-[48px] rounded-[8px] px-5 text-[14px] opacity-100 hover:opacity-60 w-[100px]"
            >
              {signInLoad ? <ButtonLoading /> : 'Sign In'}
            </button>
          </Link>
          <Link href="/sign-up">
            <button
              onClick={() => handleSignUpClick()}
              className="text-gray-2 bg-alpha-20 h-[48px] rounded-[8px] px-5 text-[14px] opacity-100 hover:opacity-60 w-[100px]"
            >
              {signUpLoad ? <ButtonLoading /> : 'Sign Up'}
            </button>
          </Link>
        </div>
      </div>
    </div>
  );
};

export default Home;
